import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import TelegramIcon from '@material-ui/icons/Telegram';
import CommentIcon from '@material-ui/icons/Comment';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import { ConvertDate } from "../utils/CommonMethods";
import { ShareBlogUrl, ShareSiteUrl } from '../configs/ShareUrlConfig';
import Metadata from './Metadata';
import { avatarImageUrl } from "../configs/SiteMetadata";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 500,
        margin: 20
    },
    msgCard: {
        margin: 20,
        padding: 20
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
}));

export default function ShowBlog(props) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    return (
        <Card className={classes.root}>
            <CardHeader
                avatar={
                    <Avatar aria-label="recipe" className={classes.avatar} src={avatarImageUrl}>
                        {/* {GetAuthorsInitial(props.blog.author)} */}
                    </Avatar>
                }
                action={
                    <IconButton aria-label="settings">
                        <MoreVertIcon />
                    </IconButton>
                }

                title={props.blog.title}
                subheader={ConvertDate(props.blog.timestamp)}
            />
            { (props.blog.imageUrl === undefined || props.blog.imageUrl === '') ?
                <div></div> :
                (<CardMedia
                    className={classes.media}
                    image={props.blog.imageUrl}
                    title={props.blog.title}
                />)
            }

            <CardContent>
                {
                    <Typography variant="body2" color="textSecondary">
                        {props.blog.content}
                    </Typography>

                }
            </CardContent>
            <CardActions disableSpacing>
                <Tooltip title="Message Author on Telegram">
                    <a href={ShareBlogUrl.TelegramChat} target="_blank">
                        <IconButton aria-label="comment">
                            <CommentIcon />
                        </IconButton>
                    </a>
                </Tooltip>
                <Tooltip title="Share on WhatsApp">
                    <a href={ShareBlogUrl.WhatsApp + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <WhatsAppIcon />
                        </IconButton>
                    </a>
                </Tooltip>
                <Tooltip title="Share on Telegram">
                    <a href={ShareBlogUrl.Telegram + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <TelegramIcon />
                        </IconButton>
                    </a>
                </Tooltip>
                <Tooltip title="Share on Facebook">
                    <a href={ShareBlogUrl.Facebook + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <FacebookIcon />
                        </IconButton>
                    </a>
                </Tooltip>
                <Tooltip title="Share on Twitter">
                    <a href={ShareBlogUrl.Twitter + props.blog.rowKey} target="_blank">
                        <IconButton aria-label="comment">
                            <TwitterIcon />
                        </IconButton>
                    </a>
                </Tooltip>

                <Tooltip title="Read More">
                    <IconButton
                        className={clsx(classes.expand, {
                            [classes.expandOpen]: expanded,
                        })}
                        onClick={handleExpandClick}
                        aria-expanded={expanded}
                        aria-label="Read more"
                    >
                        <ExpandMoreIcon />
                    </IconButton>
                </Tooltip>
            </CardActions>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent>
                    {
                        <Typography variant="body2" color="textSecondary" paragraph>{props.blog.extendedContent}</Typography>
                    }
                </CardContent>
            </Collapse>
        </Card>
    );
}